.header, .footer {
    background-color: #f0f0f0;
    border: 1px solid #c0c0c0;
    width: 100%;
    position: fixed;
    height: 18px;
    font-size: 16px;
}

.footer {
    bottom: 0px;
}

.header span.app-title {
    float: left;
    display: inline-block;
}

.header span.app-cr {
    float: right;
}

.maincontent {
    position: fixed;
    top: 19px;
    border-top: solid 1px;
    bottom: 10px;
    display: flex;
    flex-wrap: nowrap;
    background-color: white;
    height: 100%;
    width: 100%;
    padding-bottom: 40px;
}

.toolbar {
    min-width: 250px;
    background-color: white;
    border: 1px solid #c0c0c0;
    overflow-y: auto;
    margin-bottom: 39px;
    height:100%;
}

.view2d {
    height: 100%;
}

table.wide {
    width: 100%;
}

hr {
    margin: 4px;
}

button {
    display: inline-block;
    float: left;
    padding: 2px;
    margin: 0;
    margin-left: 4px;
    margin-bottom: 4px;
    border-radius: 8px;
    border: 1px solid #c0c0c0;
    font-size: 0.8em;
    background-color: #f8f8f8;
}

button:active {
    background-color: #8080b0;
}

button::-moz-focus-inner {
    border: 0;
}

div.load-button label {
    float: left;
    border-radius: 8px;
    border: 1px solid #c0c0c0;
    display: inline-block;
    padding: 2px;
    font-size: 0.8em;
    background-color: #f8f8f8;
}

div.project-load-save {
    display: flex;
    justify-content: space-between;
}

div.project-load {
    margin: 4px;
}

div.project-save {
    margin:4px;
}

div.globalsettings {
    margin: 4px;
}

hr.newline {
    clear: left;
}

div.save-svg {
    float: right;
    margin-right: 4px;
}

div.project-load-save div.c-modal--window.modal-fade-on-enter {
    max-height: 100px;
    padding: 0px;
    border: 1px solid;
    background-color: white;
}

div.project-load-save div.c-modal--window.modal-fade-on-enter div.title {
    text-align: center;
    background-color: #f0f080;
    border-bottom: solid 1px;
    min-height: 32px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 5px;
    font-size: 1.2em;
    width: 100%;
}


.Resizer {
    background: #000;
    opacity: 0.2;
    z-index: 1;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -moz-background-clip: padding;
    -webkit-background-clip: padding;
    background-clip: padding-box;
}

.Resizer:hover {
    -webkit-transition: all 2s ease;
    transition: all 2s ease;
}

.Resizer.horizontal {
    height: 11px;
    margin: -5px 0;
    border-top: 5px solid rgba(255, 255, 255, 0);
    border-bottom: 5px solid rgba(255, 255, 255, 0);
    cursor: row-resize;
    width: 100%;
}

.Resizer.horizontal:hover {
    border-top: 5px solid rgba(0, 0, 0, 0.5);
    border-bottom: 5px solid rgba(0, 0, 0, 0.5);
}

.Resizer.vertical {
    width: 11px;
    margin: 0 -5px;
    border-left: 5px solid rgba(255, 255, 255, 0);
    border-right: 5px solid rgba(255, 255, 255, 0);
    cursor: col-resize;
}

.Resizer.vertical:hover {
    border-left: 5px solid rgba(0, 0, 0, 0.5);
    border-right: 5px solid rgba(0, 0, 0, 0.5);
}
.Resizer.disabled {
    cursor: not-allowed;
}
.Resizer.disabled:hover {
    border-color: transparent;
}

div.scrollable {
    overflow-y: auto;
}

@media (max-height: 720px) {
    .toolbar {
        font-size: small;
        margin: 1px;
        padding: 1px;
    }

    div.load-button label {
        padding: 0px 4px;
        margin: 1px;
    }

    hr {
        margin: 1px;
    }
}

.center {
    position:  fixed;
    width: 600px;
    top: 40px;
    left: calc(50% - 700px);
    bottom: 40px;
    z-index: 100;
    padding: 0px;
}