input.editable {
    margin: 0;
    height: 100% !important;
    display: inline-block;
    width: 95%;
    border-radius: 0 !important;
    background-color: inherit;
    border:none;
}

div.toolpaths input.edited {
    border: solid 1px;
    background-color: white;
}

@media (max-height: 720px) {

    tr.op-prop td {
        padding: 0px;
    }

    input.editable {
        font-size: small;
    }
}