div.hidden {
    display: none;
}

div.op-settings {
    display: inline-block;
    padding: 4px;
}

div.op-settings .title {
    font-size: 0.9em;
    display: inline-block;
    float: left;
    font-weight: bold;
}

div.op-settings div.create-delete-buttons {
    float: right;
}

div.op-settings select.op-setting-select {
    float: left;
    clear: left;
    margin: 2px;
    font-size: 0.8em;
    border-radius: 8px;
    border: 1px solid #c0c0c0;
}

div.op-settings select.unit-select {
    float: right;
    clear: right;
    margin: 2px;
    font-size: 0.8em;
    border-radius: 8px;
    border: 1px solid #c0c0c0;
}


div.disable-arcs {
    float: right;
    clear: right;
    margin: 2px;
    font-size: 0.8em;
}

div.import-export div.import-button label {
    float: left;
    border-radius: 8px;
    border: 1px solid #c0c0c0;
    display: inline-block;
    padding: 2px;
    margin: 0;
    margin-left: 4px;
    margin-bottom: 4px;
    margin-top: 4px;
    font-size: 0.8em;
    background-color: #f8f8f8;
}

div.import-export button {
    margin-top: 4px;
}

div.op-settings .save-button {
    float: right;
    margin-top: 4px;
}

div.op-settings div.gen-toolpath-div {
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 4px;
}

div.op-settings div.gen-toolpath-div button.apply-to-toolpaths-button {
    float: right;
}

div.op-settings div.create-operation div.c-modal--window.modal-fade-on-enter {
    max-height: 100px;
    padding: 0px;
    border: 1px solid;
    background-color: white;
}

div.op-settings div.create-operation div.c-modal--window.modal-fade-on-enter div.title {
    text-align: center;
    background-color: #f0f080;
    border-bottom: solid 1px;
    min-height: 32px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 5px;
    font-size: 1.2em;
    width: 100%;
}


div.op-settings div.import-export div.c-modal--window.modal-fade-on-enter {
    max-height: 100px;
    padding: 0px;
    border: 1px solid;
    background-color: white;
}

div.op-settings div.import-export div.c-modal--window.modal-fade-on-enter div.title {
    text-align: center;
    background-color: #f0f080;
    border-bottom: solid 1px;
    min-height: 32px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 5px;
    font-size: 1.2em;
    width: 100%;
}

div.import-export {
    display: flex;
}