div.toolpaths {
    display: flex;
    padding: 4px;
    flex-direction: column;
    justify-content: space-between;
}

div.toolpaths div.toolpaths-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

div.toolpaths .title {
    font-size: 0.9em;
    display: inline-block;
    float: left;
    font-weight: bold;
}

div.toolpaths table.toolpath-list {
    font-size: 0.7em;
    border: 1px solid #c0c0c0;
    border-collapse: collapse;
    width: 100%;
    margin-top: 8px;
}

div.toolpaths table.toolpath-list th {
    text-align: left;
    border: 1px solid #c0c0c0;
    background-color: #e0e0e0;
}

div.toolpaths table.toolpath-list tr.odd{
    background-color: #f0f0ff;
}

div.toolpaths table.toolpath-list tr.even{
    background-color: #f8fff0;
}

div.toolpaths table.toolpath-list tr.selected {
    background-color: #8080ff;
    font-weight: bold;
}

div.toolpaths table.toolpath-list td {
    padding-left: 4px;
    padding-right: 4px;
}

div.toolpaths table.toolpath-list td.op-prop-name {
    font-weight: bold;
}

div.toolpaths table.toolpath-list td.op-prop-value {
    text-align: right;
    padding-right: 4px;
}

div.toolpaths table.toolpath-list td {
    border: 1px solid #c0c0c0;
}

div.toolpaths button {
    display: inline-block;
    float: right;
}

div.toolpaths .hidden {
    display: none;
}

div.toolpaths button.optimize-gcode {
    display: inline-block;
    float: left;
}
div.toolpaths button.edit-toolpath {
    display: inline-block;
    float: left;
}

div.toolpaths button.save-gcode {
    display: inline-block;
    float:right;
}

div.toolpaths div.toolpath-list {
    overflow-y: auto;
    width: 100%;
    height: 100%;
    amax-height: 160px;
    display: flex;
}

div.toolpaths table.opt-save {
    padding: 2px;
    margin: 2px;
    width: 100%;
}

div.toolpaths table.opt-save div.save-gcode div.c-modal--window.modal-fade-on-enter {
    max-height: 100px;
    padding: 0px;
    border: 1px solid;
    background-color: white;
}

div.toolpaths table.opt-save div.c-modal--window.modal-fade-on-enter div.title {
    text-align: center;
    background-color: #f0f080;
    border-bottom: solid 1px;
    min-height: 32px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 5px;
    font-size: 1.2em;
    width: 100%;
}

div.toolpaths table.opt-save div.c-modal--window.modal-fade-on-enter button {
    margin-right: 8px;
}

div.toolpath-ops {
    display: flex;
    min-height: 50px;
    max-height: 50px;
    padding-bottom: 40px;
    flex-direction: column;
}