.svgloader {
    display: inline-block;
    width: 80%;
    margin: 0 auto;
}

.pngloader {
    display: inline-block;
    float: left;
    width: 50%;
    margin-left: 4px;
}

input[type="file"] {
    display: none;
}

.custom-file-upload {
    border: 1px solid #c0c0c0;
    border-radius: 4px;
    display: inline-block;
    cursor: pointer;
    background-color: #f0f0f0;
    padding: 4px;
    margin-left:4px;
}

label.custom-file-upload {
    display: inline-block;
    float: left;
    margin: 2px;
}
