dialog div.title {
    text-align: center;
    background-color: #f0f080;
    border-bottom: solid 1px;
    min-height: 32px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 5px;
    font-size: 1.2em;
    width: 100%;
}



div.global-settings-panel {
    display: flex;
    flex-direction: column;
    padding: 4px;
}

div.global-settings-panel div.prop {
    display: flex;
    flex-direction: row;
    margin-top: 4px;
    justify-content: space-between;
}

div.global-settings-panel input.error {
  background-color: red;
}

div.global-settings-panel div.prop-name {
    width: 40%;
}
div.global-settings-panel div.prop-value {
    width: 30%;
}

div.global-settings-panel div.prop-value input {
    width:100%;
}

div.global-settings-panel div.prop-unit {
    width: 15%;
    padding-left: 4px;
}
div.global-settings-panel div.prop-reset {
    width: 10%;
}