div.create-operation > div.title {
    background-color: #f0f080;
    border-bottom: solid 1px;
    min-height: 32px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 5px;
    padding-left: 2px;
    font-size: 1.2em;
}


div.create-operation  div.c-modal--window.modal-fade-on-enter {
    min-height: 20%;
    min-width: 30%;
    min-heigth: 150px;
    border: 1px solid;
    font-size: 0.8em;
    overflow-y: auto;
}

div.create-operation  div.c-modal--window.modal-fade-on-enter input{
    background-color: #f0f0f0;
}