div.op-settings table.op-props {
    font-size: 0.6em;
    border: 1px solid #c0c0c0;
    border-collapse: collapse;
    width: 100%;
    margin-top: 8px;
}

div.op-settings table.op-props th {
    text-align: left;
    border: 1px solid #c0c0c0;
    background-color: #e0e0e0;
}

div.op-settings table.op-props tr.odd{
    background-color: #f0f0ff;
}

div.op-settings table.op-props tr.even{
    background-color: #f8fff0;
}

div.op-settings table.op-props td.op-prop-name {
    font-weight: bold;
}

div.op-settings table.op-props td.op-prop-value {
    text-align: right;
    padding-right: 4px;
}

div.op-settings table.op-props td.invalid {
    background-color: red;
}

div.op-settings table.op-props td {
    border: 1px solid #c0c0c0;
}


div.op-settings table.op-props input[type=number] {
    -moz-appearance:textfield;
}

div.op-settings table.op-props input.ed-prop {
    background-color: white;
}

div.op-settings table.op-props input.edited {
    border: solid 1px;
    background-color: white;
}

div.op-settings table.op-props input.invalid {
    border: solid 2px;
    border-color: red;
}

div.op-settings table.op-props td.op-prop-value select {
    width: 100%;
    border-radius: 8px;
    border: 1px solid #c0c0c0;
}