td.gcode-multisave div.c-modal--window.modal-fade-on-enter {
    max-height: 440px;
    min-height: 440px;
    padding: 0px;
    border: 1px solid;
    background-color: white;
    display: inline-block;
}

div.toolpath-choose {
    display: inline-block;
    width: 100%;
    overflow-y: auto;
    overflow-scrolling: auto;
    min-height: 320px;
    max-height: 320px;
    color: #808080;
    border-bottom: solid;
    border-width: 1px;
    border-collapse: collapse;
    padding:0px;
}

div.toolpath-choose table.toolpath-list tr.sel {
    font-weight: bold;
    color: black;
}

div.toolpath-choose table.toolpath-list {
    margin: 0px;
}

div.toolpath-choose table.toolpath-list td.order {
    width: 64px;
}

div.toolpath-choose table.toolpath-list td {
    height: 32px;
}

div.toolpath-choose table.toolpath-list td.order div:hover {
    border: solid;
    border-width: 1px;
    border-collapse: collapse;
    padding: 0px;
    margin: 0px;
}

div.choose-filename {
    display: flex;
    padding: 4px;
    position: absolute;
    bottom: 0px;
    width: 100%;
    flex-direction: row;
}

div.choose-filename div.props {
    display:flex;
    flex-direction: column;
    justify-content:left;
}

div.choose-filename div.props div {
    padding-top: 2px;
    justify-content: left;
}


div.choose-filename div.upper {
    padding-top: 2px;
    padding-bottom: 2px;
}



div.choose-filename div.comments {
    float: left;
}